<template>
    <div id="mepage">
        <BlogCard></BlogCard>
        <AppList :personal="applist"></AppList>
    </div>
</template>
<script>
import BlogCard from 'components/content/blogcard';
import AppList from 'components/content/applist';

export default {
    name: 'mepage',
    data(){
        return{
            applist:[
                {
                    img:require('assets/img/appimg/add.png'),
                    info:'我的'
                },
                {
                    img:require('assets/img/appimg/up.png'),
                    info:'更多'
                }
            ]
        }
    },
    components:{
        BlogCard,
        AppList
    }
}
</script>
<style scoped>
#mepage{
    background-color: rgba(100,100,100,.4);
}

</style>


