<template>
    <div class="listitem">
        <i>
            <img :src=app.img alt="">
        </i>
        <span>
            {{ app.info }}
        </span>
    </div>
</template>
<script>
export default {
    name: '',
    props: {
        app: {
            type: Object,
            default() {
                return {}
            }
        }
    }
}
</script>
<style scoped lang="less">
.listitem {
    width: 40px;
    height: 60px;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: space-between;
    i {
        flex:3;
        background-color: white;
        border-radius: 4px;
        img {
            width: 100%;
            height: 100%;
        }
    }
    span{
        margin-top: 3px;
        color: #fff;
    }


}
</style>


