<template>
    <div class="applist">
        <ListItemVue v-for="(item ,index) in personal" :key="index" :app="item" :index="index">

        </ListItemVue>
    </div>
</template>
<script>
import ListItemVue from './ListItem.vue';
export default {
    name: '',
    props:{
        personal:{
            type:Array,
            default(){
                return []
            }
        }
    },
    components:{
        ListItemVue
    }
}
</script>
<style scoped lang="less">
.applist{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding:5px;
    margin-top: 10px;
    background-color: rgba(100, 100, 100, .4);
    border: 1px solid rgba(100, 100, 100, .4);
}
</style>


