<template>
    <div class="cardlist">
        <div class="cardleft" v-if="index%2==0">
            <img :src="card.imgUrl" alt="">
        </div>
        <div class="cardright">
            <h2>{{card.title}}</h2>
            <p>{{card.content}}</p>
            <span>标签：{{card.sign}}</span>
        </div>
        <div class="cardleft" v-if="index%2!==0">
            <img :src="card.imgUrl" alt="">
        </div>
    </div>
</template>
<script>
export default {
    name: 'cardlist',
    props: {
        card: {
            type: Object,
            default() {
                return {
                    title: '这是最亮的标题',
                    content: '诗和内容都在远方~',
                    sign: '溜走了~',
                    imgUrl: 'https://cdn.jsdelivr.net/gh/Tony-hurry/imageBad@main//study-images/linux01.png'

                }
            }
        },
        index:{
            type:Number,
            default(){
                return 0
            }
        }

    },
    data(){
        return {
            number:this.index
        }
    }
}
</script>
<style scoped lang="less">
.cardlist{
    width:100vw;
    height:100px;
    display:flex;
    padding:5px;
    border-bottom:2px solid white;
    .cardleft{
        margin-right:5px;
        flex: 2;
        img{
            width:100%;
            height:100%;
        }
    }
    .cardright{
        flex:3;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        h2{
            flex:3
        }
        p{
            flex:5;
            color:aqua;
        }
        span{
            flex:1;
            font-size: 12px;
            text-align: right;
            color:green;
        }
    }
}
</style>


